import axios from 'axios'
const baseUrl = '/api/blogs'

const getAll = () => {
  const req = axios.get(baseUrl)
  return req.then((res) => res.data)
}

const create = (newObj) => {
  const req = axios.post(baseUrl, newObj)
  return req.then((res) => res.data)
}

const destroy = (id) => {
  const req = axios.delete(`${baseUrl}/${id}`)
  return req.then((res) => res.data)
}

const exports = {
  getAll,
  create,
  destroy,
}

export default exports
